.key-points {
  .wrapper();
  margin-bottom: 42px;

  @media (min-width: @tablet-width) {
    border-bottom: 1px solid @color-grey;
    margin-bottom: 0;
    margin-top: 46px;
  }

  @media (min-width: @desktop-width) {
    border: none;
  }
}

.key-points__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;

  @media (min-width: @tablet-width) {
    display: grid;
    row-gap: 37px;
    column-gap: 59px;
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
    column-gap: 72px;
  }
}

.key-points__item {
  min-height: 60px;
  position: relative;
  margin-bottom: 20px;

  @media (min-width: @tablet-width) {
    min-height: 280px;
    margin-right: 74px;
    margin-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    margin-right: 0;
  }

  &::before {
    content: "";
    display: inline-block;
    width: 60px;
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    background-size: 30px 32px, 60px 60px;
    background-position: center center, 0 0;

    @media (min-width: @tablet-width) {
      width: 80px;
      height: 80px;
      background-size: 30px 32px, 80px 80px;
    }
  }

  &::after {
    @media (min-width: @tablet-width) {
      content: "1";
      font: 400 280px/280px @font-secondary;
      color: #f2f2f2;
      position: absolute;
      top: -40px;
      right: 8px;
      z-index: -1;
    }
  }

  &--supplements::before {
    background-image:
      url("../img/icons-bg/icon-leaf.svg"),
      linear-gradient(@color-accent, @color-accent);
  }

  &--supplements::after {
    @media (min-width: @tablet-width) {
      content: "1";
    }
  }

  &--easy-to-use::before {
    background-image:
      url("../img/icons-bg/icon-tub.svg"),
      linear-gradient(@color-accent, @color-accent);
  }

  &--easy-to-use::after {
    @media (min-width: @tablet-width) {
      content: "2";
    }
  }

  &--product-usage::before {
    background-image:
      url("../img/icons-bg/icon-eat.svg"),
      linear-gradient(@color-accent, @color-accent);
  }

  &--product-usage::after {
    @media (min-width: @tablet-width) {
      content: "3";
    }
  }

  &--get-results::before {
    background-image:
      url("../img/icons-bg/icon-alarm.svg"),
      linear-gradient(@color-accent, @color-accent);
  }

  &--get-results::after {
    @media (min-width: @tablet-width) {
      content: "4";
    }
  }
}

.key-points__title {
  .text-title-section();
  margin: 0 0 71px 0;
}

.key-points__description {
  margin-left: 80px;

  @media (min-width: @tablet-width) {
    margin-left: 0;
    margin-top: 112px;
    margin-bottom: 0;
  }
}
