@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
body {
  margin: 0;
}
img {
  display: inline-block;
  vertical-align: middle;
}
.visually-hidden {
  position: absolute;
  opacity: 0;
}
.logo-text:hover {
  opacity: 0.8;
}
.logo-text:active,
.logo-text:focus {
  opacity: 0.6;
}
.button {
  display: inline-block;
  text-align: center;
  background-color: #68b738;
  border: 0;
  font: 400 14px/18px "Oswald", "Arial", sans-serif;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  padding-top: 10px;
  padding-bottom: 10px;
}
.button:hover {
  background-color: #5EAA2F;
}
.button:active,
.button:focus {
  color: rgba(255, 255, 255, 0.3);
}
.button--secondary {
  background-color: #f2f2f2;
  color: #444444;
}
.button--secondary:hover {
  background-color: #ebebeb;
}
.button--secondary:active,
.button--secondary:focus {
  color: rgba(68, 68, 68, 0.3);
}
