.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
}
.nav__list--closed {
  display: none;
}
.nav__list--opened {
  display: block;
  position: absolute;
  z-index: 20;
  background-color: #ffffff;
  top: 64px;
  left: 0;
  margin-top: 0;
}
