.nav__toggle {
  width: 24px;
  height: 16px;
  display: block;
  position: relative;
  border: 0;
  cursor: pointer;
  background-color: #ffffff;
}
.nav__toggle::before {
  content: "";
  width: 24px;
  height: 1px;
  background-color: #000000;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
