.main-intro {
  .wrapper();
  background-image:
    url("../img/main-intro-bg-can-mobile.png"),
    url("../img/main-intro-bg-cat-mobile.jpg");
  background-repeat: no-repeat, no-repeat;
  background-size: 280px 270px, auto 302px;
  background-position: center 167px, center 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: 22px;
  position: relative;

  @media (min-resolution: 2dppx) {
    background-image:
      url("../img/main-intro-bg-can-mobile@2x.png"),
      url("../img/main-intro-bg-cat-mobile@2x.jpg");
  }

  @media (min-width: @tablet-width) {
    margin-top: 40px;
    margin-bottom: 0;
    text-align: left;
    align-items: flex-start;
    background-image: url("../img/main-intro-bg-can-tablet.png");
    background-repeat: no-repeat;
    background-size: 709px 609px;
    background-position: center bottom;
    background-color: @color-white;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/main-intro-bg-can-tablet@2x.png");
    }
  }

  @media (min-width: @desktop-width) {
    margin-top: 0;
    margin-bottom: 87px;
    // -117px это оффсет, который зависит от высоты шапки. Если пунктов меню будет больше, оффсет надо поменять
    height: calc(694px - 117px);
    background-image: none;

    &::before {
      content: "";
      position: absolute;
      z-index: 2;
      top: 28px;
      //позиционирование относительно центра контейнера со смещением 114px по макету
      left: calc(50% - 276px + 114px);
      width: 552px;
      height: 499px;
      background-image: url("../img/main-intro-bg-can-desktop.png");
      background-repeat: no-repeat;
      background-size: 552px 499px;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/main-intro-bg-can-desktop@2x.png");
      }
    }

    &::after {
      content: "";
      position: absolute;
      // -117px это оффсет, который зависит от высоты шапки. Если пунктов меню будет больше, оффсет надо поменять
      top: -117px;
      right: 0;
      width: 50%;
      height: 694px;
      background-image: url("../img/main-intro-bg-cat-desktop.jpg");
      background-repeat: no-repeat;
      background-size: cover;

      @media (min-resolution: 2dppx) {
        background-image: url("../img/main-intro-bg-cat-desktop@2x.jpg");
      }
    }
  }
}

.main-intro__title {
  margin-top: 28px;
  margin-bottom: 0;
  position: relative;
  z-index: 4;
  font: normal 400 36px/36px @font-secondary;
  color: @color-white;

  @media (min-width: @tablet-width) {
    font: 60px/60px @font-secondary;
    color: @color-black;
    margin-left: 87px;
    margin-top: 23px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 111px;
    margin-left: 80px;
  }
}

.main-intro__shop-slogan {
  margin-top: 25px;
  margin-bottom: 0;
  position: relative;
  z-index: 4;
  font: normal 400 14px/14px @font-secondary;
  color: @color-white;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    color: @color-black;
    font: 20px/20px @font-secondary;
    margin-top: 39px;
    margin-left: 89px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 80px;
  }
}

.main-intro__button {
  margin-top: 302px;
  align-self: stretch;
  position: relative;
  z-index: 4;
  padding-top: 10px;
  padding-bottom: 10px;

  @media (min-width: @tablet-width) {
    margin-top: 49px;
    margin-left: 90px;
    margin-bottom: 526px;
    padding: 17px 27px;
    font: 20px/20px @font-secondary;
    display: inline-block;
    align-self: flex-start;
  }

  @media (min-width: @desktop-width) {
    margin-left: 80px;
    margin-top: 53px;
    margin-bottom: 185px;
  }
}
